import { Reducer } from 'redux';
import {
  CharacterPlannerState,
  AppThunkAction,
  actionSelectCharacter,
  actionSetActiveTabIndex,
  actionUpdateCharacter,
  actionSetHasLoaded,
  actionSetCharacterState,
  SelectCharacterAction,
  SetActiveTabIndexAction,
  UpdateCharacterAction,
  SetHasLoadedAction,
  SetCharacterStateAction,
  Character,
  NormalizedList
} from '.';
import {
  selectCharacter,
  setActiveTabIndex,
  updateCharacter,
  setHasLoaded,
  setCharacterState
} from '../actions';
import { initialState } from './initialState';
import { saveCharacterState } from '../utils';

type KnownAction =
  SelectCharacterAction |
  SetActiveTabIndexAction |
  UpdateCharacterAction |
  SetHasLoadedAction |
  SetCharacterStateAction;

export const actionCreators = {
  selectCharacter: (id: string): AppThunkAction<KnownAction> => (dispatch, getState) => {
    // set the active tab back to the first tab
    dispatch(setActiveTabIndex(0));
    // then select the character
    dispatch(selectCharacter(id));
  },
  setActiveTabIndex,
  updateCharacter: (character: Character): AppThunkAction<KnownAction> => (dispatch, getState) => {
    dispatch(updateCharacter(character));
    // save the state of all characters to local storage
    saveCharacterState(getState().characterPlannerState.characters);
  },
  setCharacterState: (characters: NormalizedList<Character>): AppThunkAction<KnownAction> => (dispatch, getState) => {
    dispatch(setCharacterState(characters));
    dispatch(setHasLoaded(true));
  }
};

export const reducer: Reducer<CharacterPlannerState> = (state = initialState, incomingAction) => {
  const action = incomingAction as KnownAction;

  switch (action.type) {
    case actionSelectCharacter:
      return {
        ...state,
        selectedCharacterId: (action as SelectCharacterAction).id
      };
    case actionSetActiveTabIndex:
      return {
        ...state,
        activeTabIndex: (action as SetActiveTabIndexAction).index
      };
    case actionUpdateCharacter:
      return {
        ...state,
        characters: {
          byId: {
            ...state.characters.byId,
            [(action as UpdateCharacterAction).character.id]: (action as UpdateCharacterAction).character
          },
          allIds: [...state.characters.allIds]
        }
      };
    case actionSetHasLoaded:
      return {
        ...state,
        hasLoadedFromStorage: (action as SetHasLoadedAction).hasLoaded
      };
    case actionSetCharacterState:
      return {
        ...state,
        characters: (action as SetCharacterStateAction).characters
      };
    default:
      return state;
  }
};
